#skip-links a:focus, #skip-links a:active {
  display: block;
}

#block-menu-menu-about-us {
  display: none;
}

#meanmenu-dest {
  margin: 0 auto 1em auto;
}

.mean-container {
  position: relative;
  z-index: 50;
  top: 0;
  right: 0;
  left: 0;
  clear: both;
}

.mean-container .mean-bar {
  position: absolute;
  top: -9em;
  width: 100%;
  margin: 0 auto;
  padding: 1em;
}

@media (min-width: 62.1rem) {
  .mean-container .mean-bar {
    top: -6em;
  }
}

.mean-container .mean-bar a {
  position: relative;
  display: block;
  color: #FFFFFF !important;
}

.mean-container .mean-bar a.meanmenu-reveal {
  right: 1em !important;
  float: right;
  width: 2.5em;
  height: 2.5em;
  color: #FFFFFF !important;
}

.mean-container .mean-bar a.meanmenu-reveal span {
  position: relative;
  display: block;
  height: 5px;
  background: #FFFFFF;
}

.mean-container .mean-bar a.meanmenu-reveal span:nth-child(2) {
  margin: .4em 0;
}

.mean-container .mean-bar a.meanmenu-reveal span:last-child {
  position: absolute;
  bottom: 0;
  background: none;
}

.mean-container .mean-bar a.meanmenu-reveal + .mean-nav {
  background: #00283e;
  /* The Fallback */
  background: rgba(0, 40, 62, 0.95);
}

.mean-container .mean-bar a.meanclose span:not(:last-child) {
  position: relative;
  transform: rotate(45deg);
  transform-origin: center center;
}

.mean-container .mean-bar a.meanclose span:first-child {
  top: 5px;
  transform: rotate(-45deg);
}

.mean-container .mean-bar a.meanclose span:nth-child(2) {
  margin: 0;
}

.mean-container .mean-bar a.meanclose + .mean-nav {
  padding: 1em;
}

.mean-container .mean-bar nav.mean-nav {
  position: relative;
  clear: both;
  margin-top: 4em;
}

.mean-container .mean-bar nav.mean-nav ul {
  margin: 0;
}

.mean-container .mean-bar nav.mean-nav ul.menu .menu {
  margin-top: .75em;
}

.mean-container .mean-bar nav.mean-nav ul.menu .menu .expanded .active {
  color: rgba(255, 255, 255, 0.7) !important;
}

.mean-container .mean-bar nav.mean-nav ul.menu .mean-expand {
  font-size: 21px !important;
  font-weight: bold;
  position: absolute;
  top: 0;
  right: 0;
  display: inline-block;
  min-width: 2em;
  padding: .25em .6em;
  text-align: center;
}

.mean-container .mean-bar nav.mean-nav ul.menu .mean-expand:hover, .mean-container .mean-bar nav.mean-nav ul.menu .mean-expand:focus, .mean-container .mean-bar nav.mean-nav ul.menu .mean-expand:active {
  text-decoration: none;
}

.mean-container .mean-bar nav.mean-nav ul li {
  padding: .75em 0 .75em .75em;
  border-bottom: solid 1px #FFFFFF;
}

.mean-container .mean-bar nav.mean-nav ul li:last-child {
  border: none;
}

.mean-container .mean-bar nav.mean-nav ul li a {
  display: inline;
}

.mean-container .mean-bar nav.mean-nav ul > li {
  position: relative;
}

.mean-container .mean-bar nav.mean-nav ul > li li {
  margin-left: 21px;
  border-color: rgba(255, 255, 255, 0.5);
}

#block-menu-block-2 {
  display: none;
}

@media (min-width: 62.1rem) {
  #block-menu-block-2 {
    display: block;
  }
}

.block-menu-block {
  float: right;
  clear: both;
}

.block-menu-block .menu-name-menu-header-links-menu {
  margin-bottom: 28px;
}

.block-menu-block .menu-name-menu-header-links-menu .menu li {
  display: inline-block;
  margin: 0;
  padding: 0 .5em;
}

.block-menu-block .menu-name-menu-header-links-menu .menu li:nth-child(n):not(:last-child) {
  border-right: solid 1px #FFFFFF;
}

.block-menu-block .menu-name-menu-header-links-menu .menu li a {
  color: #FFFFFF !important;
}

.block-search {
  position: relative;
  clear: both;
  max-width: 95%;
  margin: 0 auto;
}

@media (min-width: 62.1rem) {
  .block-search {
    z-index: 500;
    margin-top: 3em;
    margin-right: 6em;
  }
}

.block-search form {
  position: relative;
}

@media (min-width: 62.1rem) {
  .block-search form {
    float: right;
  }
}

.block-search form .form-type-textfield {
  margin: .5em auto;
}

@media (min-width: 62.1rem) {
  .block-search form .form-type-textfield {
    float: none;
    width: auto;
  }
}

.block-search form .form-type-textfield input {
  padding: .75em;
  color: #FFFFFF;
  border: none;
  background: rgba(255, 255, 255, 0.3);
}

.block-search form .form-type-textfield input::placeholder {
  color: #FFFFFF;
}

.block-search form .form-type-textfield input[type='text'] {
  width: calc(100% - 3em);
}

.block-search form .form-actions {
  position: absolute;
  top: .5em;
  right: 0;
}

.block-search form .form-actions .form-submit {
  font-size: 0;
  position: relative;
  z-index: 10;
  top: 0;
  width: 25px;
  height: 25px;
  cursor: pointer;
  color: white;
  border: solid 4px white;
  border-radius: 50%;
  background-color: transparent;
}

.block-search form .form-actions:after {
  position: absolute;
  z-index: 9;
  right: -3px;
  bottom: -4px;
  width: 13px;
  height: 4px;
  content: '';
  transform: rotate(45deg);
  transform-origin: bottom right;
  background: #FFFFFF;
}

